<template>
    <f7-page>

        <!-- Logo -->
        <f7-block>

            <div class="center">
                <img src="../assets/logo.png">
            </div>

            <!-- Login form -->
            <f7-list>

                <!-- User -->
                <f7-list-item>
                    <f7-label>Matrícula del vehículo</f7-label>
                    <f7-input type="text"></f7-input>
                </f7-list-item>

                <!-- Password -->
                <f7-list-item>
                    <f7-label>Contraseña</f7-label>
                    <f7-input type="password"></f7-input>
                </f7-list-item>

            </f7-list>


            <!-- Submit -->
            <f7-list-item id="submit-btn">
                <f7-button href="/check-page">Entrar en Gescotrans</f7-button>
            </f7-list-item>

        </f7-block>

        <Footer/>

    </f7-page>
</template>

<script>
    import Footer from '../layout/Footer';

    export default {
        name: 'LoginPage',
        components: {Footer},
        data() {
            return {
                //
            };
        }
    };
</script>

<style scoped>
    .button {
        padding: 25px;
        line-height: 1px;
        text-transform: uppercase;
    }

    #submit-btn {
        list-style: none;
        margin: 50px 0;
    }
</style>


